.callback__wrap {
  margin-bottom: 128px;
  display: flex;
  justify-content: space-between;
}
.callback__head {
  max-width: 489px;
}
.callback__title {
  margin-bottom: 24px;
}
.callback__subtitle {
  font-weight: 400;
  font-size: 18px;
  line-height: 133%;
  color: #0e1016;
}
.callback__form {
  flex: 1 1 auto;
  max-width: 660px;
}

.form__row {
  margin-bottom: 16px;
  display: flex;
  gap: 16px;
}
.form__label {
  flex: 1 1 50%;
}
.form__input {
  width: 100%;
  padding: 14px 24px;
  border-radius: 10px;
  background: #f2f2f2;
  font-family: "Onest", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: var(--black);
  border: 1px solid transparent;
  transition: 0.2s ease-in;
}
.form__input:focus {
  outline: none;
  border: 1px solid var(--blue);
  transition: 0.2s ease-in;
}
.form__input::placeholder {
  font-family: "Onest", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: #727272;
}
.form__textarea {
  width: 100%;
  height: 104px;
  resize: none;
  padding: 14px 24px;
  border-radius: 10px;
  background: #f2f2f2;
  font-family: "Onest", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: var(--black);
  border: 1px solid transparent;
  transition: 0.2s ease-in;
}
.form__textarea:focus {
  outline: none;
  border: 1px solid var(--blue);
  transition: 0.2s ease-in;
}
.form__textarea::placeholder {
  font-family: "Onest", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  color: #727272;
}
.form__btn {
  width: 100%;
  margin-top: 30px;
  display: block;
  padding: 14px 100px;
  font-family: "Onest", sans-serif;
  font-weight: 500;
  font-size: 18px;
  text-align: center;
  color: #fff;
  border-radius: 10px;
  background: #015c75;
  transition: 0.2s ease-in;
}
.form__btn:focus {
  outline: none;
  background: #004d5f;
  transition: 0.2s ease-in;
}
.form__btn:hover {
  outline: none;
  background: #004d5f;
  transition: 0.2s ease-in;
}

@media all and (max-width: 1000px) {
  .callback__wrap {
    margin-bottom: 120px;
    display: flex;
    justify-content: unset;
    gap: 32px;
    flex-direction: column;
  }
  .callback__head {
    max-width: 100%;
  }
  .callback__title {
    margin-bottom: 16px;
  }
  .callback__subtitle {
    font-weight: 400;
    font-size: 18px;
    line-height: 133%;
    color: #0e1016;
  }
  .callback__form {
    flex: 1 1 auto;
    max-width: 100%;
  }
  .form__row {
    margin-bottom: 24px;
    display: flex;
    gap: 24px;
  }
  .form__label {
    flex: 1 1 50%;
  }
}
@media all and (max-width: 500px) {
  .callback__wrap {
    margin-bottom: 72px;
    display: flex;
    justify-content: unset;
    gap: 24px;
    flex-direction: column;
  }
  .callback__subtitle {
    font-weight: 400;
    font-size: 16px;
    line-height: 137%;
    color: #0e1016;
  }
  .form__row {
    margin-bottom: 16px;
    display: flex;
    gap: 16px;
    flex-direction: column;
  }
  .form__label {
    flex: 0 0 auto;
  }
  .form__btn {
    margin-top: 24px;
    white-space: nowrap;
    padding: 14px 0;
    text-align: center;
  }
}